.tab-menu {
  text-align: center;
  margin-bottom: 2rem;

  .tab {
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 1rem;
    text-underline-offset: 1rem;
    color: white;
  }

  .active-tab {
    border-bottom: 1px solid var(--accent);
    color: var(--accent);
  }
}
