.tags-container {
  align-self: flex-start;
  flex-wrap: wrap;
  font-size: 0.6rem;
  padding: 1rem;
  background-color: var(--highlight);
  border: 1px solid var(--light);
  border-radius: 5px;
  padding-bottom: 4rem;
  position: absolute;
  width: calc(var(--sidebar-width) - 1rem);
  top: 1rem;
  right: 1rem;

  .active {
    background-color: var(--accent);
  }

  h2 {
    font-size: 1rem;
    margin: 1rem 0 2rem 0;
    font-weight: 600;

    span {
      margin-right: 0.5rem;
    }
  }

  .tags {
    justify-content: flex-start;
    gap: 0.5rem;

    .navlink {
      opacity: 0.8;
      text-transform: uppercase;
      letter-spacing: 1px;
      display: inline-block;
      font-size: 0.6rem;
      cursor: pointer;

      &:hover {
        opacity: 1;
      }
    }
  }
}
