.floating-window {
  align-items: flex-end;

  .confirmation-alert-box {
    max-width: 30vw;
    border-radius: 8px;
    padding: 2rem;
    border: 1px solid var(--light);

    h2 {
      font-size: 3rem;
      font-weight: 600;
      margin-bottom: 5rem;
    }
    p {
      font-size: 0.8rem;
      margin-block: 2rem;
      line-height: 1.6;
      color: var(--light);
    }

    .options {
      font-size: 0.8rem;
      font-weight: 600;
      letter-spacing: 1px;
      color: white;
      display: block;
      width: 100%;
      text-align: center;
      margin-top: 1rem;
      padding: 1rem 0;
      border-radius: 100px;
      border: 1px solid var(--light);
      text-transform: uppercase;

      &.active {
        background-color: var(--accent);
      }
    }
  }
}
