// HEADER

.main-header {
  height: var(--header-height) !important;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  background-color: var(--dark);

  .navlink {
    font-size: 1.8rem;
  }

  .login-btn {
    padding: 0;
    margin: 0 1rem;
    font-size: 0.9rem;
    button {
      cursor: pointer;
      background: unset !important;
      color: white;
    }
  }

  .flex {
    gap: 2rem;
  }

  .search-bar {
    width: 40vw;
    position: relative;

    input {
      width: 100%;
      border-radius: 100px;
      background-color: var(--highlight);
      border: none;
      font-size: 1rem;
      color: white;
      padding: 0.8rem 3rem;

      &:focus {
        outline: 1px solid white;
      }

      &::-webkit-search-cancel-button {
        appearance: none;
      }
    }

    span {
      position: absolute;
      z-index: 999;
      font-size: 1.2rem;
    }

    .search-icon {
      left: 20px;
      height: 100%;
      justify-content: center;
      color: var(--light);
    }

    .close-icon {
      height: 100%;
      // inset: 0;
      right: 1rem !important;
      justify-content: center;
    }
  }
}

.header-avatar img {
  border-radius: 50%;
  background-color: white;
  aspect-ratio: 1/1;
  width: calc(var(--header-height) / 1.8);
  object-fit: cover;
  border: 1px solid var(--light);
}
